import React from 'react';

type Props = $ReadOnly<{|
  children: React.Node,
  classLabel?: string,
|}>;

function Section({children, classLabel}: Props): React.component {
  return (
    <div className={'section-container ' + (classLabel != null ? classLabel: 'normal-section')}>
      {children}
    </div>
  );
}

export default Section;
