/* global chrome */

const internalHostNames = new Set(['internalfb.com', 'our.internmc.facebook.com', 'fb.workplace.com', 'fb.quip.com']);
export default function genPrivacySensitiveURL(callback) {
  chrome.tabs.query({active: true, currentWindow: true}, tabs => {
    const url = new URL(tabs[0].url);
    const hostname = trimWWW(url.hostname);
    if(internalHostNames.has(hostname)) {
      callback(hostname, url.pathname, url.search);
    } else {
      callback('External_Site', null, null);
    }
  });
}

function trimWWW(hostname) {
  return hostname.replace('www.', '');
}
