import {gen, genSet} from './LocalStorage.js';
import getInternURL from './getInternURL.js';

const CSRF = getInternURL('/api/dtsg/internal');
const CSRF_KEY = 'oneoneext/settings/csrftoken/v1';

async function _genCSRFToken() {
  const item = await gen(CSRF_KEY);
  const now = Date.now() / 1000 + 60;
  if (item != null && item.expire >= now) {
    return item.token;
  }
  const response = await fetch(CSRF);
  if (response.status !== 200) {
    console.warn('Failed GraphQL fetch. Status Code: ' + response.status);
    return null;
  }
  const text = await response.text();
  if (text == null) {
    console.warn('Failed to get CSRF token');
    return null;
  }
  const token = JSON.parse(text.slice(9));
  await genSet(CSRF_KEY, token);
  return token.token;
}

// memoize up to 2hrs so we only send one csrf request at a time
let _memo = null;
let _memoTime = null;
export default function() {
  const now = Date.now() / 1000;
  if (_memo === null || _memoTime <= now) {
    _memo = _genCSRFToken(CSRF_KEY);
    _memoTime = now + 7200;
  }
  return _memo;
}
