/**
 * Root UI component
 */
import React from 'react';

type Props = $ReadOnly<{|
  value: bool,
  text: string,
  onClick: ()=>void,
|}>;

function Checkbox({value, text, onClick}: Props): React.component {
  const inputFocus = React.useRef(null);
  return (
    <div className="checkbox-label-container" onClick={()=>{
      inputFocus.current.focus();
      onClick();
    }}>
      <div tabindex="-1" ref={inputFocus} className="checkbox-container">
        <div className="checkbox-image-placeholder">
          <img src="checkmark.png" alt=""
            className={"checkbox-checkmark " + (value ? 'checkbox-selected' : '')} />
        </div>
      </div>
      <div className="checkbox-label-text">
        {text}
      </div>
    </div>
  );
}

export default Checkbox;
