/* global chrome */

import getInternURL from '../graphql/getInternURL';

export function genGoToURL(hasMgrLandingAccess: boolean, employeeID?: ?string) {
  const managerPath = employeeID != null ? '/manager/?tab=ONEONE&id=' : '/manager/?tab=ONEONE';
  const path = hasMgrLandingAccess ? managerPath : '/conversations/overview/';
  const extraParam = employeeID == null ? '' : employeeID;
  return getInternURL(path + extraParam);
}

export function genLinkText(hasMgrLandingAccess: boolean) {
  return hasMgrLandingAccess ? 'Go to Manager Landing' : 'Go to 1:1 Tool';
}
