import React from 'react';
import './App.css';

import Form from './components/Form.react';
import Confirmation from './components/Confirmation.react';
import Nux from './components/Nux.react';
import OptedOut from './components/OptedOut.react';
import OptOutConfirmationStep from './components/OptOutConfirmationStep.react';

import {load, persist, genClear, gen, genSet} from './graphql/LocalStorage.js';
import {ROOT_QUERY} from './graphql/PersistentDocumentIds';
import {genCachedGraphQL} from './graphql/GraphQL';
import genLog from './graphql/genLog';
import useEffectOnce from './util/useEffectOnce.react';
import {useState, useEffect, useCallback, useRef} from 'react';
import initErrorLogging from './util/initErrorLogging';

const SETTINGS_KEY = 'oneoneext/settings/v1';
const CONFIRMATION_VALUES_KEY = 'oneone_persisted_confirmation_values';

const TEN_MINUTES = 10 * 60;
const THIRTY_MINUTES = 30 * 60;

export type AppSettings = $ReadOnly<{|
  showNux: ?boolean,
  optedOut: ?boolean,
|}>;

function App() {
  initErrorLogging();
  const settingsRef = useRef(null);
  const [
    submittedDirectReportDetails,
    setSubmittedDirectReportDetails,
  ] = useState(null);
  const [hasMgrLandingAccess, setHasMgrLandingAccess] = useState(false);
  const [showOptOutConfirmation, setShowOptOutConfirmation] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [hasOptedOut, setHasOptedOut] = useState(false);
  const [showNux, setShowNux] = useState(false);
  const [teamDetails, setTeamDetails] = useState([]);
  const [xfnDetails, setXFNDetails] = useState([]);

  const markHasOptedOut = useCallback((choice: boolean) => {
    const newSettings = {
      ...settingsRef.current,
      optedOut: choice,
    };
    settingsRef.current = newSettings;
    setHasOptedOut(choice);
    genSet(SETTINGS_KEY, newSettings);
  }, []);

  const markShowNux = useCallback((choice: boolean) => {
    const newSettings = {
      ...settingsRef.current,
      showNux: choice,
    };
    settingsRef.current = newSettings;
    setShowNux(choice);
    genSet(SETTINGS_KEY, newSettings);
  }, []);

  useEffect(() => {
    if (submittedDirectReportDetails == null) {
      load(CONFIRMATION_VALUES_KEY, setSubmittedDirectReportDetails);
    } else {
      persist(
        CONFIRMATION_VALUES_KEY,
        submittedDirectReportDetails,
        TEN_MINUTES,
      );
    }
  }, [submittedDirectReportDetails]);

  useEffectOnce(() => {
    genLog({event: 'IA_ACTION_ONE_ONE_CHROME_EXTENSION_OPEN'});
    gen(SETTINGS_KEY).then((storedSettings: ?AppSettings) => {
      settingsRef.current = storedSettings;
      setHasOptedOut(storedSettings?.optedOut === true);
      // if it's null, default to showing the NUX
      setShowNux(
        storedSettings == null ||
          storedSettings.showNux == null ||
          storedSettings.showNux === true,
      );
      genCachedGraphQL(ROOT_QUERY, null, THIRTY_MINUTES, (data, wasCached) => {
        const manager = data?.one_one_extension_root?.manager;
        const teamMembers = data?.one_one_extension_root?.teamMember ?? [];
        const xfnPartners = data?.one_one_extension_root?.xfnPartners ?? [];
        teamMembers.sort((a, b) =>
          (a.firstName ?? '').localeCompare(b.firstName),
        );
        xfnPartners.sort((a, b) =>
          (a.firstName ?? '').localeCompare(b.firstName),
        );
        setHasMgrLandingAccess(
          data?.one_one_extension_root?.viewer_has_mgrlanding_access === true,
        );
        // We should update the local storage with the server values
        // only it's fresh data from the server, otherwise we might
        // risk overwritting some recent change from the user
        if (!wasCached) {
          markHasOptedOut(data?.one_one_extension_root?.hasOptedOut === true);
        }
        setTeamDetails(format([manager, ...teamMembers]));
        setXFNDetails(format(xfnPartners));
        setIsLoading(false);
      });
    });
  });

  const title = hasMgrLandingAccess ? 'Manager Landing' : '1:1 Tool';

  return (
    <div className="App">
      {showNux && (
        <Nux
          title={title}
          markShowNux={markShowNux}
          markHasOptedOut={markHasOptedOut}
          setShowOptOutConfirmation={setShowOptOutConfirmation}
        />
      )}
      {showOptOutConfirmation && (
        <OptOutConfirmationStep
          title={title}
          markShowNux={markShowNux}
          markHasOptedOut={markHasOptedOut}
          setShowOptOutConfirmation={setShowOptOutConfirmation}
        />
      )}
      {hasOptedOut && (
        <OptedOut title={title} markHasOptedOut={markHasOptedOut} />
      )}
      {submittedDirectReportDetails == null && !showNux && !hasOptedOut && (
        <Form
          title={title}
          isInitialRequestLoading={isLoading}
          setSubmittedDirectReportDetails={setSubmittedDirectReportDetails}
          hasMgrLandingAccess={hasMgrLandingAccess}
          setHasMgrLandingAccess={setHasMgrLandingAccess}
          markShowNux={markShowNux}
          teamDetails={teamDetails}
          xfnDetails={xfnDetails}
          setShowOptOutConfirmation={setShowOptOutConfirmation}
        />
      )}
      {submittedDirectReportDetails != null && !showNux && !hasOptedOut && (
        <Confirmation
          submittedDirectReportDetails={submittedDirectReportDetails}
          hasMgrLandingAccess={hasMgrLandingAccess}
          clearData={() => {
            genClear(CONFIRMATION_VALUES_KEY);
            setSubmittedDirectReportDetails(null);
          }}
        />
      )}
    </div>
  );
}

function format(employees) {
  return employees
    .filter(emp => emp != null)
    .map(emp => {
      return {
        employeeFBID: emp.fbid,
        firstName: emp.firstName,
        photo: emp.profilePicture?.uri,
      }
    });
}

export default App;
