/**
 * Root UI component
 */
import React from 'react';

type ProfilePhotoProps = $ReadOnly<{|
  photo: ?string,
|}>;

function ProfilePhoto({photo}: ProfilePhotoProps): React.component {
  return (
    <div className="profile-photo">
      <img src={photo} alt="" height={24} width={24} className="profile-photo-image"/>
    </div>
  );
}

export default ProfilePhoto;
