/* global chrome */

import StackTrace from 'stacktrace-js';
import genLog from '../graphql/genLog';
export default function initErrorLogging() {
  window.onerror = function(msg, url, line, col, error) {
    try {
      var callback = function(stackframes) {
        var stringifiedStack = stackframes.map(sf => sf.toString()).join('\n');
        genLog({event: 'ONE_ONE_EXT_JS_ERROR', payload: [
          {key: 'error_msg', value: msg},
          {key: 'stack_trace', value: stringifiedStack},
        ]});
      };
      StackTrace.fromError(error).then(callback);
    } catch (e){
      // avoid infinite loop
    }
    // Supress error when true
    return true;
  };
}
