/**
 * Root UI component
 */
import React from 'react';
import ProfilePhoto from './ProfilePhoto.react';
import {genGoToURL} from '../util/genCTALink';

type DirectReportLinkProps = $ReadOnly<{|
  employeeFBID: string,
  firstName: string,
  photo: ?string,
  successfullySaved: bool,
  hasMgrLandingAccess: bool,
|}>;

function DirectReportLink({
  employeeFBID,
  firstName,
  photo,
  successfullySaved,
  hasMgrLandingAccess,
}: DirectReportLinkProps): React.component {
  const path = hasMgrLandingAccess ? '/manager/?tab=ONEONE&id=' : '/conversations/overview/';
  const link =  genGoToURL(hasMgrLandingAccess, employeeFBID);
  return (
    <div className={successfullySaved ? 'direct-report-link-cell':'direct-report-link-error-cell'}>
      <div className="direct-report-link-cell-image">
        <ProfilePhoto photo={photo} />
      </div>
      <div className="direct-report-link-cell-text-container">
        <div className="direct-report-link-cell-text">{firstName}</div>
        {!successfullySaved && <div className="direct-report-link-cell-error-text">Error Uploading</div>}
      </div>
      <a href={link} target="_blank" rel="noopener noreferrer">
        <button alt="Open 1:1 tool" className="direct-report-link-button grey-button" type="button">
          <img src="go-to_outline_24.png" alt="" className="direct-report-link-button-icon"/>
        </button>
      </a>
    </div>
  );
}

export default DirectReportLink;
