/* global chrome */

import genCSRFToken from './genCSRFToken.js';
import {gen, genv, genClear} from './LocalStorage.js';
import xhr from './xhr.js';
import getInternURL from './getInternURL.js';

export function genGraphQL(fbid, variables) {
  return genCSRFToken()
    .then(csrf => {
      let url = getInternURL('/api/graphql?doc_id=' + fbid);
      if (variables) {
        url += '&variables=' + encodeURIComponent(JSON.stringify(variables));
      }
      url += '&fb_dtsg=' + csrf + '&__a=1';
      return xhr(url);
    })
    .then(response => {
      const json = JSON.parse(response);
      if (json.error) {
        console.warn('Failed GraphQL fetch. Status Code: ' + json.error);
        return null;
      }
      return json != null ? json.data : null;
    });
}

export async function genAsyncGraphQL(fbid, variables) {
  return genCSRFToken()
    .then(csrf => {
      let url = getInternURL('/api/graphql?doc_id=' + fbid);
      if (variables) {
        url += '&variables=' + encodeURIComponent(JSON.stringify(variables));
      }
      url += '&fb_dtsg=' + csrf + '&__a=1';
      return xhr(url);
    })
    .then(response => {
      const json = JSON.parse(response);
      if (json.error) {
        console.warn('Failed GraphQL fetch. Status Code: ' + json.error);
        return null;
      }
      return json != null ? json.data : null;
    });
}

const KEY_ROOT = 'oneoneext/cached_graphql/v2/';

export async function cleanup(forceClear) {
  if (forceClear) {
    console.log('Forcing clear of local cache');
  }
  const items = await genv(null);
  const now = Math.floor(Date.now() / 1000);
  const weekAgo = now - 86400 * 7;
  const keysToRemove = [];
  for (let key in items) {
    if (key.startsWith(KEY_ROOT)) {
      // Remove keys expired a week ago to keep storage clean
      const item = items[key];
      if (forceClear || item.expiration < weekAgo) {
        keysToRemove.push(key);
      }
    } else if (!key.startsWith('oneoneext/settings/')) {
      keysToRemove.push(key);
    }
  }
  if (keysToRemove.length > 0) {
    await genClear(keysToRemove);
  }
}

export function genCachedGraphQL(fbid, variables, timeout, cb) {
  let key = KEY_ROOT + fbid;
  if (variables) {
    key += JSON.stringify(variables);
  }
  const now = Math.floor(Date.now() / 1000);
  gen(key).then(data => {
    function _gen() {
      return genGraphQL(fbid, variables).then(payload => {
        if (payload == null) {
          // just keep the old data for now
          return;
        }
        const data = {
          payload,
          expiration: now + (timeout || 86400),
        };
        chrome.storage.local.set({[key]: data});
        cb(payload, false);
      });
    }
    if (data != null) {
      cb(data.payload, true);
    }
    if (data == null || data.expiration < now) {
      _gen();
    }
  });
}
