/* global chrome */
import {genGraphQL} from './GraphQL.js';
import {LOG_EVENT_MUTATION} from './PersistentDocumentIds';

const version = chrome.runtime.getManifest().version;

type KeyValuePayload = {|
  key: string,
  value: string,
|};

type Props = {|
  event: string,
  action: ?string,
  payload?: $ReadOnlyArray<KeyValuePayload>,
|};

export default function genLog({event, action, payload}: Props) {
  genGraphQL(LOG_EVENT_MUTATION, {
    input: {event, action, version, log_payload: payload},
  });
}
